html,
body,
#react-root {
  height: 100%;
}

body {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  margin: 0px;
}

img {
  display: inline-block;
  max-width: 100%;
}

/* --------------------------------------------------------------------------------
Container
-------------------------------------------------------------------------------- */

.container {
  margin-top: 60px;
  padding: 20px;
}

/* --------------------------------------------------------------------------------
Button
-------------------------------------------------------------------------------- */

.btn {
  display: inline-block;
  padding: 10px 20px;
  border-color: #014041;
  border-width: 1px 1px 3px;
  border-radius: 4px;
  background-color: olivedrab;
  color: #f8f8f8;
  font-size: 1.1rem;
  outline: 0;
  cursor: pointer;
}

.btn_sizeMin {
  min-height: 50px;
  min-width: 100px;
}

.btn_sizeSm {
  padding: 10px 15px;
  font-size: 0.8rem;
}

.btn_sizeFull {
  display: block;
  width: 100%;
}

.btn:hover {
  background-color: #AA94D0;
  text-decoration: none;
}

/* This line makes the "Register" and "Login" buttons look like links */
.btn_asLink {
  padding: 0;
  border-width: 0;
  border-bottom: 1px solid #444;
  border-radius: 0;
  background: none;
  font: inherit;
  color: #444;
}

.btn_asLink:hover {
  background-color: transparent;
  border-color: #AA94D0;
  color: #008183;
}

/* --------------------------------------------------------------------------------
Alert
-------------------------------------------------------------------------------- */

.alert {
  box-sizing: border-box;
  padding: 15px 10px;
  margin: 15px 0;
  border: 2px solid #e01616;
  background-color: #efefef;
  border-radius: 3px;
  color: #e01616;
  font-size: 0.8rem;
}

/* --------------------------------------------------------------------------------
Form Treatment
-------------------------------------------------------------------------------- */

.formPanel {
  box-sizing: border-box;
  width: 400px;
  max-width: 100%;
  padding: 25px;
  margin: 0 auto 20px;
  background-color: #efefef;
  border-radius: 3px;
  box-shadow: 0 1px 2px rgb(0, 0, 0, 0.4);
  display: flex;
  flex-direction: column;
  align-items: center;
}

input {
  margin: 10px 5px;
}

img {
  max-width: 100%;
  max-height: 300px;
}

#addCommentform {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  margin-top: 10px;
}

/* Map */

.mapContainer {
  margin-bottom: 44.5px;
}

/* Components */

.component {
  margin: 80px 0 10px 0;
}

.loginMessage {
  margin-left: 10px;
  margin-top: 0px;
}

/* user page */
.welcomeMessage {
  font: 1.5em sans-serif;
  text-align: center;
}

.avatarConatainer {
  display: flex;
  flex-direction: row;
  align-content: center;
  justify-content: center;
  margin-bottom: 20px;
}

.userAvatar {
  height: 3em;
  margin-left: 5px;
}

/* 
---------------------------------------------------------------------------
*/

#mapBottom {
  height: 2px;
  background-color: white;
}

/* *********** */

footer {
  margin-top: 0px;
  margin-left: -15px;
  padding: 15px;
  text-align: center;
  font-size: 0.75em;
  background-color: darkolivegreen;
  position: fixed;
  bottom: 0;
  width: 100vw;
  z-index: 2;
}